:root {
  --color: #111111;
  --font-family-sans-serif: "Helvetica Neue", Arial, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --font-family-monospace: "Fira Code", Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: "Helvetica Neue", Arial, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-family: var(--font-family-sans-serif);
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: 'scrollbar';
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html, body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
}

@-ms-viewport {
  width: device-width;
}

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

a {
  color: #37a866;
}

a:hover {
  color: #111111;
}

code {
  background: none;
  font-family: "Fira Code", Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  font-family: var(--font-family-monospace);
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

code.prop {
  font-weight: 700;
}

#root {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.container {
  display: flex;
  width: 100%;
  max-width: 700px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  text-align: center;
}

.intro {
  min-height: 500px;
  min-height: 50vh;
  padding-top: 16em;
  padding-bottom: 12em;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.responsive-table {
  max-width: 100%;
  overflow-x: auto;
  margin-bottom: 2em;
}

.title + .text {
  margin-top: 0 !important;
}

.actions {
  display: flex;
  justify-content: center;
}

.button em {
  font-size: 70%;
  font-style: normal;
  display: inline-block;
  vertical-align: middle;
  opacity: 0.5;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 1rem;
  background-color: transparent;
  font-size: 15px;
  line-height: 1.45;
}

p code,
table code {
  padding: 0.35rem 0.75rem;
  color: #111111;
  background-color: rgb(0, 0, 0, 0.051);
  border-radius: 2px;
}

table th,
table td {
  padding: 0.85rem 2rem 0.85rem 0.5rem;
  vertical-align: top;
  border-width: 0;
}

table thead th {
  vertical-align: bottom;
}

table td {
  color: #666666;
}

table tbody tr:nth-child(odd) {
  background-color: rgb(245, 245, 245);
}

/* Text component */
.text {
  color: var(--color);
  font-size: 18px;
  margin: 18px 0;
  line-height: 1.45;
}

.text__inherit {
  font-size: inherit;
  line-height: inherit;
  margin-top: 0;
}

code[class*="language-"],
pre[class*="language-"] {
	color: #ABB2BF;
	background: none;
	text-shadow: 0 1px rgba(0, 0, 0, 0.3);
	font-family: "Fira Code", Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;

	-moz-tab-size: 4;
	tab-size: 4;

	-webkit-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
}

/* Code blocks */
pre[class*="language-"] {
	padding: 1em;
	margin: .5em 0;
	overflow: auto;
	border-radius: 0.13em;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	background: #282A36;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: .3em;
	white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
	color: slategray;
}

.token.punctuation {
	color: #f8f8f2;
}

.namespace {
	opacity: .7;
}

.token.property,
.token.tag,
.token.constant,
.token.symbol,
.token.deleted {
	color: #f92672;
}

.token.boolean,
.token.number {
	color: #ae81ff;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
	color: #6EBA76;
}

.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable {
	color: #f8f8f2;
}

.token.class-name {
	color: #E5C07B;
}

.token.function {
	color: #61AFEF;
}

.token.atrule,
.token.attr-value {
	color: #e6db74;
}

.token.operator {
	color: #4CB6C2;
}

.token.keyword {
	color: #C678DD;
}

.token.regex,
.token.important {
	color: #fd971f;
}

.token.important,
.token.bold {
	font-weight: bold;
}
.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}

.command-line-prompt {
	border-right: 1px solid #999;
	display: block;
	float: left;
	font-size: 100%;
	letter-spacing: -1px;
	margin-right: 1em;
	pointer-events: none;

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.command-line-prompt > span:before {
	color: #999;
	content: ' ';
	display: block;
	padding-right: 0.8em;
}

.command-line-prompt > span[data-user]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] $";
}

.command-line-prompt > span[data-user="root"]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] #";
}

.command-line-prompt > span[data-prompt]:before {
	content: attr(data-prompt);
}

/* Anchor component */
.anchor {
  display: block;
  float: left;
  vertical-align: middle;
  line-height: 1.45;
  margin-left: -30px;
  padding-right: 4px;
  visibility: hidden;
}

/* Title component */
.title {
  color: var(--color);
  margin: 0 auto 16px;
}

.title__h1 {
  font-size: 44px;
  line-height: 1.2;
}

.title__h2 {
  font-size: 38px;
  line-height: 1.2;
}

.title__h3 {
  font-size: 22px;
  line-height: 1.2;
  font-weight: 600;
}

.title__h4 {
  font-size: 20px;
  line-height: 1.2;
  font-weight: 400;
  color: #666666;
}

.title__h5 {
  font-size: 20px;
  line-height: 1.2;
}

.title__h6 {
  font-size: 18px;
  line-height: 1.2;
}

/* Button component */
.button {
  border: 1px solid transparent;
  border-radius: 2px;
  box-shadow: 0 2px 1px rgba(0,0,0,.084);
  cursor: pointer;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.button:focus,
.button:active {
  outline: none;
}

.button__block {
  display: block;
  width: 100%;
}

.button__default {
  display: block;
  height: 44px;
  padding: 0 19px;
  font-size: 15px;
  line-height: 41px;
}

.button_small {
  display: block;
  height: 30px;
  padding: 0 12px;
  font-size: 14px;
  line-height: 28px;
}

.button__large {
  line-height: 1.6;
  height: 60px;
  padding: 14px 24px;
  font-size: 18px;
  border-width: 2px;
}

.button__primary {
  color: #fff;
  background-color: #111;
}

.button__primary:hover {
  background-color: #000;
}

.button__primary.button__outline {
  color: #111;
  background-color: #fff;
  border-color: #111;
}

.button__primary.button__outline:hover {
  color: #fff;
  background-color: #000;
  border-color: #000;
}

.button__secondary {
  color: #fff;
  background-color: #3cb46e;
}

.button__secondary:hover {
  background-color: #37a866;
}

/* Rule component */
.rule {
  border-top: 1px solid #DDDDDD;
  margin-top: 24px;
  margin-bottom: 24px;
}

/* Examples section */
#examples > .container {
  max-width: 1200px !important;
}

#examples pre[class*="language-"] {
  padding: 0 !important;
}

.demos {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.demos .demo {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  text-align: left;
  display: flex;
  flex-direction: column;
}

.demos .demo .button {
  margin-top: auto;
}

@media (min-width: 991px) {
  .demos .demo {
    flex: 0 0 33.333333%;
    max-width: 33.333333%;
  }
}

/* PhotoGrid component */
.grid-container {
  position: relative;
  overflow: hidden;
}

.grid {
  line-height: 0;
  display: flex;
  flex-wrap: wrap;
  padding-left: 3px;
  padding-right: 3px;
}

.grid .column {
  flex-basis: 0;
  flex-grow: 1;
  flex: 0 0 12.5%;
  max-width: 12.5%;
  padding-left: 3px;
  padding-right: 3px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.grid .column .picture {
  margin: 3px 0;
  cursor: pointer;
  min-width: 100%;
  max-width: 100%;
  height: auto !important;
  background-color: #ECECEE;
  overflow: hidden;
  position: relative;
}

.grid .column .picture:last-child {
  margin-bottom: 0;
}

.grid .column .picture.loading {
  cursor: default;
}

.grid .column .picture .bg {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition-delay: 0.82s;
  transition: 0.75s ease all;
}

.grid .column .picture img {
  width: 100%;
  height: auto;
  display: block;
  opacity: 0;
  visibility: hidden;
}

.grid .column .picture.loading .bg,
.grid .column .picture.loading img {
  opacity: 0;
}

.grid .column .picture.loaded img {
  position: relative;
}

.grid .column .picture.loaded img,
.grid .column .picture.loaded .bg {
  opacity: 1;
  z-index: 0;
  transition: 0.75s ease all;
  transition-delay: 0s;
}

.grid .column .picture.loaded:hover img,
.grid .column .picture.loaded:hover .bg {
  -webkit-transform: scale(1.02);
  transform: scale(1.02);
}

.grid .column .picture.loaded::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: 0;
  z-index: 1;
  transition: 0.45s ease all;
}

.grid .column .picture.loaded:hover::before {
  opacity: 0.5;
}

@media (min-width: 1200px) {
  .grid-container {
    padding-bottom: 2em;
  }
}

.github-corner {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1000;
}

.github-corner:hover .octo-arm {
  -webkit-animation: octocat-wave 560ms ease-in-out;
  animation: octocat-wave 560ms ease-in-out;
}

@-webkit-keyframes octocat-wave {
  0%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0)
  }
  20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg)
  }
  40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg)
  }
}

@keyframes octocat-wave {
  0%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0)
  }
  20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg)
  }
  40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg)
  }
}

@media (max-width: 500px) {
  .github-corner:hover .octo-arm {
    -webkit-animation: none;
            animation: none;
  }

  .github-corner .octo-arm {
    -webkit-animation: octocat-wave 560ms ease-in-out;
            animation: octocat-wave 560ms ease-in-out;
  }
}

/* Header component */
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #FFFFFF;
  z-index: 100;
  box-shadow: 0 4px 12px rgba(0,0,0,.08), 0 0 1px rgba(1,0,0,.1);
  display: flex;
  justify-content: center;
  align-content: center;
}

.header .navigation {
  margin-left: auto;
  margin-right: auto;
}

.header .navigation .nav {
  width: 100%;
  display: flex;
  padding-left: 0;
  margin-bottom: 0;
  margin-top: 0;
  list-style: none;
  justify-content: center;
}

.header .navigation .nav > li {
  padding-left: 10px;
  padding-right: 10px;
}

.header .navigation .nav > li > a {
  display: block;
  padding-top: 20px;
  padding-bottom: 20px;
  text-decoration: none;
  color: #999999;
}

.header .navigation .nav > li > a:hover {
  color: #111111;
}

.header .right {
  margin-left: auto;
  position: absolute;
  right: 0;
  height: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
}

@media (max-width: 650px) {
  .header {
    overflow-x: auto;
  }

  .header .navigation .nav > li {
    padding-left: 5px;
    padding-right: 5px;
  }

  .header .navigation .nav > li > a {
    font-size: 15px;
  }

  .header .right {
    display: none;
  }
}

/* Section component */
.section {
  font-size: 16px;
  padding-top: 5em;
  padding-bottom: 5em;
  line-height: 1.45;
}

.section .content {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2em;
  padding-right: 2em;
}

